import React from 'react'
import vs from './images/vs.png'
import cm from './images/cm.png'
import cm2 from '../../Components/Media/cm.jpg'
import ulb_data from '../../Components/Common/DynamicData'

const Branding = () => {
  const {brand_tag,
    brand_name,
    ulb_name,
    state_logo} = ulb_data()
  return (
    <>
       <div 
       className='relative w-full h-full'>
                {/* <div className='absolute h-full w-full top-0 opacity-20' style={{ backgroundImage: `url(${vs})`, backgroundSize: 'cover', backgroundRepeat: 'repeat', backgroundPositionY : '-50vh' }}> </div> */}
                <div className='absolute h-full w-full top-0 opacity-20'> </div>
                <div className='flex justify-between px-2 sm:px-20 py-1.5 items-center'>
                  {/* <img src={brand_tag == 'AMC' ? state_logo : state_logo} alt="" className='w-16 sm:w-20 h-16 sm:h-20 block border-2 shadow-md rounded-full transform -scale-x-100' /> */}
                  <img src={brand_tag == 'AMC' ? cm2 : cm} alt="" className='w-16 sm:w-20 h-16 sm:h-20 block border-2 shadow-md rounded-full transform -scale-x-100' />
                  <div className='flex flex-col items-center justify-center'>
                    <span className='text-sm sm:text-4xl font-bold text-center uppercase'>{brand_name}</span>
                    <span className='text-xs sm:text-base'>All Public Services At Your Fingertip</span>
                  </div>
                  <img src={state_logo} alt="" className='w-16 sm:w-20 h-16 sm:h-20 block border-2 shadow-md rounded-full' />
                </div>
        </div>
                
    </>
  )
}

export default Branding