import React, { useState, useEffect } from 'react'
import shape from '../../Components/Media/shape.svg'
import SimpleImageSlider from "react-simple-image-slider"
import videoHead from '../../../src/assets/videoHead.mp4'
import imgBanner1 from '../../Components/Media/imgBanner1.png'
import imgBanner2 from '../../Components/Media/imgBanner2.png'
import { useNavigate } from 'react-router-dom'

function ImgHeader() {

    const navigate = useNavigate()

    const [activeIndex, setActiveIndex] = useState(0);
    // const images = [banner1, banner2, banner3];
    const images = [imgBanner1, imgBanner2];

    useEffect(() => {
        const interval = setInterval(() => {
            setActiveIndex((prevIndex) => (prevIndex + 1) % images.length);
        }, 3000); // Change slide every 3 seconds

        return () => {
            clearInterval(interval);
        };
    }, [images.length]);

    const handleClick = (index) => {
        setActiveIndex(index);
    };
    return (
        <>
            <div className="flex items-center justify-center h-screen">
                <div className="relative w-full h-[85%] -top-[3.8vw] overflow-hidden ">
                    {/* <div className='w-100% '>
                        <video className='w-full ' autoplay="true" loop muted>
                            <source src={videoHead} type="video/mp4" />
                        </video>
                    </div> */}
                    <div className='md:ml-[24rem] lg:ml-[24rem] '>


                        <SimpleImageSlider
                            width='80%'
                            height={900}
                            images={images}
                            showBullets={true}
                            showNavs={true}
                            loop={true}
                            duration={0.5}
                            autoPlay={true}
                        />
                    </div>

                    <div className="absolute -left-[30vw] -top-[10vw] hidden md:block lg:block">
                        SVG Code
                        <img className='w-[90vw]' src={shape} />
                        <div className="absolute -top-80 w-full h-full flex flex-col items-center justify-center">
                            <div className="text-4xl font-bold mb-2 text-[#99B37C]">Bringing</div>
                            <div className="text-4xl font-bold text-black mb-2">Governance</div>
                            <div className="text-4xl font-bold text-black mb-2">to Your Fingertips</div>
                            <hr className='border-2 w-20 mb-2 border-[#99B37C]' />
                            <h1 className="text-md text-black font-PublicSans w-[27rem] mb-6">Unveiling world bank funded e-Governance initiative for jharkhand.Simplify the ULB's services with e-Governance application for urban local.</h1>

                            <button className='flex  bg-amber-500 hover:bg-amber-600 hover:shadow-xl px-14 py-2 rounded-full shadow-md text-white' onClick={() => navigate('/login')}>
                                Login
                                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" class="w-6 h-6 ">
                                    <path stroke-linecap="round" stroke-linejoin="round" d="M17.25 8.25L21 12m0 0l-3.75 3.75M21 12H3" />
                                </svg>

                            </button>
                        </div>


                    </div>
                </div>
            </div>
        </>
    )
}

export default ImgHeader